<script lang="ts">
import { defineComponent } from 'vue'
import Hook2 from './hook2.vue'
export default defineComponent({
    name: 'HelloWorld',
    components: { Hook2 },
    props: {
        msg: String,
        enableCompApiService: {
            type: Boolean,
            default: true,
        },
        api: {
            type: Array,
            default: () => {
                return [
                    {
                        apiMethod: 'system.login',
                        respMatch: '@data@',
                        queryParamsMatch: {
                            a: '{a.c}',
                            c: '{msg}',
                        },
                        subApis: [
                            {
                                apiUrl: '/api1.json',
                                respMatch: '@data@',
                                queryParamsMatch: {
                                    a: '{a.c}',
                                    c: '{msg}',
                                },
                                mergeRelations: [
                                    {
                                        system: 'system',
                                    },
                                ],
                            },
                        ],
                    },
                ]
            },
        },
    },
    data() {
        return {
            a: {
                c: 1123,
            },
        }
    },
    watch: {
        a: {
            handler(newVal, oldVal) {
                debugger
                console.log('newVal', newVal)
                console.log('oldVal', oldVal)
            },
            deep: true,
        },
    },
    // computed: {
    //     $apiServiceOpts(): object {
    //         return {
    //             overrideMethod: {
    //                 $loadServicesConfig: this.$loadServicesConfig,
    //             },
    //         }
    //     },
    // },
    mounted() {
        this.$watch(
            () => this.a,
            (newVal: unknown, oldVal: unknown) => {
                debugger
                console.log('newVal', newVal)
                console.log('oldVal', oldVal)
            },
            {
                deep: true,
                immediate: false,
            }
        )
    },
    methods: {
        $loadServicesConfig() {
            console.log(' $loadServicesConfig111111111111')
        },
        $renderData(fetchData: { rendered: boolean; renderData?: [] }) {
            const { rendered = false, renderData = [] } = fetchData
            if (rendered !== true && renderData && renderData.length > 0) {
                // 进行数据渲染
                fetchData.rendered = true
                console.log(renderData)
            }
        },
    },
})
</script>

